// PDF
import html2Canvas from 'html2canvas'
import JsPDF from 'jspdf'
export default {
    install(Vue, options) {
        Vue.prototype.getPdf = function () {
            var title = this.htmlTitle
            // html2Canvas(document.querySelector('#pdfDom'), { //id
            //idpdfDom
            html2Canvas(document.body, { //body
                useCORS: true, //CORS
                allowTaint: true,
                dpi: 300, //
                // width: 490, //canvas
                // height: 240, //canvas
                // x: 0, //x
                // y: 0, //y
                async: false, //
                foreignObjectRendering: true, //ForeignObject
            }).then(function (canvas) {
                let contentWidth = canvas.width
                let contentHeight = canvas.height
                let pageHeight = contentWidth / 592.28 * 841.89 // pdfhtmlcanvas;
                let leftHeight = contentHeight //pdfhtml
                let position = 0 //pdf
                //a4[595.28,841.89]htmlcanvaspdf
                let imgWidth = 595.28
                let imgHeight = 592.28 / contentWidth * contentHeight
                let pageData = canvas.toDataURL('image/jpeg', 1.0)
                let PDF = new JsPDF('', 'pt', 'a4')
                // htmlpdf(841.89)
                //pdf
                if (leftHeight < pageHeight) {
                    PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
                } else {
                    while (leftHeight > 0) {
                        PDF.addImage(pageData, 'JPEG', 20, position, imgWidth, imgHeight)
                        leftHeight -= pageHeight
                        position -= 841.89
                        if (leftHeight > 0) {
                            PDF.addPage()
                        }
                    }
                }
                PDF.save(title + '.pdf')
            })
        }
    }
}
